import React, { Component } from 'react'
import Link from 'gatsby-link'

import { Container, Col, Row } from 'reactstrap'
import styled from 'styled-components'

import IndexSection from '../components/IndexSection'

import Gallery from 'react-photo-gallery'
import Lightbox from 'react-images'

const BodyContainer = styled(Container)`
  background-color: #000000;
  font-family: 'Ubuntu', sans-serif;
`

const images = [
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_1.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_8.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_8.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_3.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_4.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_6.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_5.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_7.png',
    width: 1632,
    height: 1045,
  },
  {
    src:
      'https://s3-us-west-2.amazonaws.com/skyh-public-assets/portfolio_image_2.png',
    width: 1632,
    height: 1045,
  },
]

class IndexPage extends Component {
  constructor() {
    super()
    this.state = { currentImage: 0 }
  }

  openLightbox = (event, obj) => {
    event.preventDefault()
    this.setState({
      currentImage: obj.index,
      lightboxIsOpen: true,
    })
  }

  closeLightbox = () => {
    this.setState({
      currentImage: 0,
      lightboxIsOpen: false,
    })
  }

  gotoPrevious = () => {
    this.setState({
      currentImage: this.state.currentImage - 1,
    })
  }

  gotoNext = () => {
    this.setState({
      currentImage: this.state.currentImage + 1,
    })
  }

  render() {
    return (
      <BodyContainer className="p-5" fluid={true}>
        <IndexSection
          header="MISSION"
          text="To create meaningful experiences for others through the development of technology and the telling of important stories."
        />
        <IndexSection
          header="RECENT"
          text={
            <div>
              Projects for{' '}
              <a target="__blank" href="https://www.truebase.io">
                Truebase
              </a>,{' '}
              <a target="__blank" href="https://www.azuqua.com">
                Azuqua
              </a>, and{' '}
              <a target="__blank" href="https://www.jump2go.com">
                Jump2Go
              </a>.
            </div>
          }
        />
        <IndexSection
          header="PROJECTS"
          text="11 websites, 9 apps, 40 videos, 15 articles, and 1 game."
        />
        <IndexSection
          header="FEATURED"
          text={
            <div>
              <a
                target="__blank"
                href="https://techcrunch.com/2016/12/04/wandered-space/"
              >
                wandered.space
              </a>,{' '}
              <a
                target="__blank"
                href="https://azuqua.com/2016/11/mission-accomplished-flo-force-one"
              >
                FLO Force One
              </a>,{' '}
              <a
                target="__blank"
                href="https://s3-us-west-2.amazonaws.com/skyh-public-assets/Hope's_End_Rules.pdf"
              >
                Hope's End
              </a>,{' '}
              <a
                target="__blank"
                href="https://skylerhartle.itch.io/new-heights"
              >
                New Heights
              </a>,{' '}
              <a target="__blank" href="https://calm-river-4553.herokuapp.com/">
                Weatherwords
              </a>,{' '}
              <a target="__blank" href="http://www.stayalert.org">
                StayAlert
              </a>
            </div>
          }
        />
        <IndexSection
          header="AWARDS"
          text="2016 GE Minds & Machines $25,000 Grand Prize, 2016 TechCrunch Disrupt London Hackathon Runner-Up, 2015 RadioWorld Best of Show Award"
        />
        <IndexSection
          header="WHAT I DO"
          text="Software Development, Narrative Design, Game Design, System Design, Narrative Direction, Creative Writing, Strategy, Content Development, Technical Writing, Scriptwriting, User Interface and Experience, Web Design"
        />
        {/* <Row className="pt-5 mb-5">
        <Col className="text-secondary" xs="2">PORTFOLIO</Col>
          <Col xs={{ size: 8, offset: 1}}>
          <Gallery photos={images} onClick={this.openLightbox} backdropClosesModal={true} enableKeyboardInput={true} />
          </Col>
        </Row>
        <Lightbox
          images={images}
          onClose={this.closeLightbox}
          onClickPrev={this.gotoPrevious}
          onClickNext={this.gotoNext}
          currentImage={this.state.currentImage}
          isOpen={this.state.lightboxIsOpen}
          width="1200"
        /> */}
      </BodyContainer>
    )
  }
}

export default IndexPage



